import {ModuleWithProviders, NgModule} from '@angular/core';

import {VdlOptionModule, OverlayModule, OVERLAY_PROVIDERS} from '../core';
import {CommonModule} from '@angular/common';
import {VdlAutocomplete} from './autocomplete';
import {VdlAutocompleteTrigger} from './autocomplete-trigger';
export * from './autocomplete';
export * from './autocomplete-trigger';

@NgModule({
  imports: [VdlOptionModule, OverlayModule, CommonModule],
  exports: [VdlAutocomplete, VdlOptionModule, VdlAutocompleteTrigger],
  declarations: [VdlAutocomplete, VdlAutocompleteTrigger],
})
export class VdlAutocompleteModule {
  /** @deprecated */
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: VdlAutocompleteModule,
      providers: [OVERLAY_PROVIDERS]
    };
  }
}
